"use client";

import { useEffect, useMemo, useState } from "react";
import DayTabs from "@/components/day-tabs";
import ShiftCard from "@/components/shift-card";
import { shifts, toISODateLocal } from "@/lib/mock";

function getDaysToEndOfMonth(from: Date) {
  const start = new Date(from.getFullYear(), from.getMonth(), from.getDate());
  const end = new Date(from.getFullYear(), from.getMonth() + 1, 0); // last day of month

  const out: string[] = [];
  const cur = new Date(start);

  while (cur <= end) {
    out.push(toISODateLocal(cur));
    cur.setDate(cur.getDate() + 1);
  }
  return out;
}

// Автообновление выбранного дня в полночь (и табов — если сменился месяц)
function useAutoTodayRollover(onRollover: (now: Date) => void) {
  useEffect(() => {
    const tick = () => {
      const now = new Date();
      onRollover(now);
    };

    // 1) таймер до ближайшей полуночи
    const now = new Date();
    const nextMidnight = new Date(now.getFullYear(), now.getMonth(), now.getDate() + 1, 0, 0, 2);
    const msToMidnight = nextMidnight.getTime() - now.getTime();

    const t1 = window.setTimeout(() => {
      tick();
      // 2) страховочный интервал раз в минуту
      const t2 = window.setInterval(tick, 60_000);
      // eslint-disable-next-line consistent-return
      return () => window.clearInterval(t2);
    }, msToMidnight);

    return () => window.clearTimeout(t1);
  }, [onRollover]);
}

export default function ShiftsClient() {
  const [q, setQ] = useState("");

  // "Сегодня" (локально)
  const [today, setToday] = useState<Date>(() => new Date());
  const [selectedDay, setSelectedDay] = useState<string>(() => toISODateLocal(new Date()));

  const days = useMemo(() => getDaysToEndOfMonth(today), [today]);

  // если selectedDay вдруг “выпал” (например, месяц сменился) — корректируем
  useEffect(() => {
    if (!days.includes(selectedDay)) {
      setSelectedDay(days[0] ?? toISODateLocal(new Date()));
    }
  }, [days, selectedDay]);

  useAutoTodayRollover((now) => {
    // если наступил новый день — пересчитываем today, табы и переводим selection на новый "сегодня"
    setToday(now);
    setSelectedDay(toISODateLocal(now));
  });

  const filtered = useMemo(() => {
    const s = q.trim().toLowerCase();

    return shifts
      .filter((x) => x.date === selectedDay)
      .filter((x) => {
        if (!s) return true;
        return [x.title, x.company, x.city].some((v) => v.toLowerCase().includes(s));
      });
  }, [q, selectedDay]);

  return (
    <div className="space-y-4">
      {/* календарь табами */}
      <DayTabs days={days} value={selectedDay} onChange={setSelectedDay} />

      {/* поиск */}
      <div className="rounded-2xl border border-zinc-200 bg-white p-4">
        <input
          value={q}
          onChange={(e) => setQ(e.target.value)}
          placeholder="Поиск: профессия, компания, город…"
          className="w-full rounded-xl border border-zinc-200 bg-white px-3 py-2 text-sm outline-none focus:ring-2 focus:ring-zinc-900/10"
        />
      </div>

      {/* список смен выбранного дня */}
      {filtered.length === 0 ? (
        <div className="rounded-2xl border border-zinc-200 bg-white p-5 text-sm text-zinc-600">
          На выбранную дату смен нет. Листай дни табами сверху.
        </div>
      ) : (
        <div className="space-y-3">
          {filtered.map((s) => (
            <ShiftCard key={s.id} shift={s} />
          ))}
        </div>
      )}
    </div>
  );
}
